.card {
  display: flex;
  flex-direction: column;

  width: 200px;
  padding: 30px 14px 15px;
  border: 1px solid #f3f3f3;
  transition: 0.3s box-shadow;

  font-family: "Roboto", sans-serif;

  &:hover {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
  }

  &__img {
    width: 160px;
    height: 134px;
    margin-left: 5px;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    margin-top: 45px;
  }

  &__code {
    font-size: 11px;
    margin-top: 5px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
    font-size: 11px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 20px;
  }

  &__price-text {
    font-size: 11px;
  }

  &__price-amount {
    font-weight: bold;
    font-size: 16px;
  }
  &__btn {
    width: 170px;
    height: 40px;
    margin-top: 20px;
    padding-top: 11px;
  
    display: flex;
    justify-content: center;
  
    background: #00acdc;
    border-radius: 5px;
    transition: color 0.3s, background-color 0.3s, border 0.3s;
  
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    color: #fff;
    text-decoration: none;

    &:hover {
      background-color: #fff;
      border: 1px solid #00acdc;
      color: #00acdc;
    }
  }
}
